module.exports = "\n\nbutton:focus {\n  outline: none;\n  box-shadow: none;\n}\n\na,\na:hover {\n  text-decoration: none;\n  display: inline-block;\n  -webkit-transition: 0.3s ease-in-out;\n  transition: 0.3s ease-in-out;\n}\n\nimg {\n  display: inline-block;\n  max-width: 100%;\n}\n\n@media (min-width: 1200px) {\n  .container {\n    max-width: 1300px;\n  }\n}\n\n.box {\n  margin: 5rem 0 15rem;\n}\n\n.heading {\n  text-align: center;\n  position: relative;\n  font-size: 3.3rem;\n  font-weight: bold;\n  margin: 3rem 0;\n  margin-top: -60px;\n  color: #fff;\n\n}\n\n.box figure {\n  -webkit-transition: 1s all;\n  transition: 1s all;\n  position: relative;\n}\n\n.box figure img {\n  height: 300px;\n  -o-object-fit: cover;\n  object-fit: cover;\n  -webkit-transition: .3s all;\n  transition: .3s all;\n  -webkit-backface-visibility: hidden;\n  backface-visibility: hidden;\n}\n\n.box figure figcaption {\n  position: absolute;\n  bottom: -50px;\n  left: 8%;\n  right: 8%;\n  background: #fff;\n  color: #000;\n  padding: 20px 15px;\n  box-shadow: 0 5px 10px #00000033;\n  -webkit-transition: .5s all;\n  transition: .5s all;\n}\n\n.box figure figcaption::after {\n  left: 50%;\n  -webkit-transform: translateX(-50%);\n  transform: translateX(-50%);\n  bottom: 0;\n  height: 2px;\n  background: #0fafb4;\n  content: '';\n  width: 0;\n  position: absolute;\n  -webkit-transition: .5s all;\n  transition: .5s all;\n}\n\n.box figure figcaption h5 {\n  font-size: 1.6rem;\n  font-weight: bold;\n  -webkit-transition: .3s all;\n  transition: .3s all;\n}\n\n.box figure figcaption h3 {\n  font-size: 2.1rem;\n  font-weight: 500;\n}\n\n.box figure:hover {\n  -webkit-transform: translateY(5px);\n  transform: translateY(5px);\n}\n\n.box figure:hover h5 {\n  color: #0fafb4;\n}\n\n.box figure:hover img {\n  box-shadow: 0px 0px 6px #0000004f, -10px -10px 1px #0fafb4;\n}\n\n.box2 figure:hover figcaption {\n  -webkit-transform: translateY(-7px);\n  transform: translateY(-7px);\n  box-shadow: 0 10px 10px